.home-page-services-block {
    display: flex;
    flex-direction: row;
    font-family: 'LarkenDEMO-Regular';
    justify-content: center;
    margin-top: 120px;
    gap: 30px;
    background-image: url(../../../images/path.png);
    background-repeat: no-repeat;
    background-position: -58px 538px;

}

.home-page-first-block,
.home-page-last-block {
    align-self: center;
}

.container {
    position: relative;
    text-align: center;
    color: white;
}

.photo-text {
    position: absolute;
    bottom: 4px;
    text-align: left;
    background: linear-gradient(to bottom, transparent, #1b1b1b);
    padding: 41px 20px 30px 30px;
}

.photo-text-block-header {
    font-size: 1.6rem;
    line-height: 31px;
    margin-bottom: 10px;
}

.photo-text-shortinfo {
    font-family: 'Roboto';
    font-size: 1rem;
    line-height: 19px;
}

.home-page-middle-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
}

.home-page-services {
    font-size: 1rem;
    line-height: 21px;
    letter-spacing: 0.3em;
    color: #FFC978;
    margin-bottom: 13px;
}

.home-page-specializations {
    font-size: 4rem;
    letter-spacing: 0.5px;
    color: #FFFFFF;
    margin-bottom: 52px;
}

.home-page-middle {
    display: flex;
    gap: 30px;
}


@media (max-width: 82.5rem) {

    .home-page-first-block,
    .home-page-last-block {
        display: none;
    }

    .home-page-middle-block {
        align-items: flex-start;
    }

}

@media screen and (max-width: 48rem) {

    .imgTest {
        width: 100%;
    }

    .home-page-services-block {
        margin: 120px 30px 0px;
        gap: 30px;
        background-image: none;
        justify-content: center;
    }

    .photo-text {
        padding: 41px 20px 30px 20px;
    }

    .home-page-middle {
        gap: 20px;
    }

}

@media screen and (max-width: 37.5rem) {

    .home-page-services-block {
        overflow: auto;
        white-space: nowrap;
        justify-content: flex-start;
    }

    .photo-text-shortinfo {
        display: none;
    }

    .photo-text {
        width: 100%;
    }

    .imgTest {
        min-width: 260px;
    }

    .home-page-middle {
        display: flex;
        gap: 30px;
        flex-direction: row;
        align-items: flex-end;
        flex-wrap: nowrap;
        justify-content: flex-start;

    }

    .home-page-middle-block {}

}

@media screen and (max-width: 32.5rem) {

    .home-page-specializations {
        font-size: 3rem;
    }

}

@media screen and (max-width: 420px) {

    .home-page-specializations {
        font-size: 2rem;
    }

}